import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router';
import './Home.css';
import { Button, ConfigProvider } from 'antd';
import slide1 from '../../images/slide1.jpeg';
import slide2 from '../../images/slide2.jpeg';
import slide3 from '../../images/slide3.jpeg';

interface Slide {
  src: string;
}

const PageHome =()=>{

    // Navigator
    const navigate = useNavigate();

    // Images
    const slides: Slide[] = [
        {src: slide1},
        {src: slide2},
        {src: slide3}
    ];

    // Function to go to the next slide
    const [currentSlideIndex, setCurrentSlideIndex] = useState(0);
    const goToNextSlide = () => {
        setCurrentSlideIndex((prevIndex) =>{
            if(prevIndex === 2){
                return 0;
            }else{
                return (prevIndex + 1);
            }
        });
    };

    // Effect for auto-play
    let interval: NodeJS.Timeout;
    useEffect(() => {
        interval = setInterval(goToNextSlide, 4000);
    });

    const handleFB =()=>{
        window.open('https://www.facebook.com/share/15ZPqAZd7i/?mibextid=wwXIfr');
    }

    const handleWA =()=>{
        window.open('https://web.whatsapp.com/send/?phone=%2B8562077997848&context=AffK_JUJWw_nlZM6KL7p3Sh86V3sgVsn33qxvJ0PK_GN4lP4LIGrvO98BeH9Ezn7KIbCCIPzbo6PAcZAMNh3K0xnNp31VN3M9p4i9cyEXR6CQffI0yt7Dt9JIUitW1iRbzIiP1ZuBKSrkIH9arLESN9CFg&source=FB_Page&app=facebook&entry_point=page_cta&fbclid=IwY2xjawLHGa9leHRuA2FlbQIxMABicmlkETFLZ2NNdkpNU2cxWHBhR2M1AR6NgWl_D5lpIhmrsWwC-5KF6zKUbM2eTSmNkWcSrJ2XCXqcVSnO7H-VLzQFiA_aem_FswuUSnows32zl57jlRR3w');
    }

    const handleGoRegis =()=>{
        clearInterval(interval);
        navigate('/register');
    }

    return(
        <div>
            <div className='homeImage' />
            <div className='homeNav'>
                <div className='homeLogo' />
                <p className='homeLogin'>ເຂົ້າສູ່ລະບົບ</p>
                <p className='contact'>ຊ່ອງທາງການຕິດຕໍ່:</p>
                <div className='facebook' onClick={handleFB} />
                <div className='whatsapp' onClick={handleWA} />
            </div>
            <div style={{display: 'inline', position: 'absolute', overflow: 'hidden', width: 620, height: 498, left: 'calc(50% + 10px)', top: 'calc(50% - 195px)', borderRadius: 6}}>
                <iframe className='trialStudy' src='https://drive.google.com/file/d/1NicdkNBR-Pw7cS-W020YTNleNudMC1gt/preview' allow='fullscreen' />
                <p className='trialTitle'>ຕົວຢ່າງວິດີໂອການສອນ:</p>
            </div>
            <div className="slideshow-container">
                <div className="slides-wrapper" style={{transform: `translateX(-${currentSlideIndex * 100}%)`}}>
                    {slides.map((slide) => (
                        <div className="slide">
                        <img src={slide.src} />
                        </div>
                    ))}
                </div>
            </div>
            
            <ConfigProvider theme={{ token: { colorPrimary: '#34495E' } }}>
                <Button className='btnRegister' type='primary' onClick={handleGoRegis}>
                    ລົງທະບຽນຮຽນທັນທີ
                </Button>
            </ConfigProvider>
        </div>
    );
}

export default PageHome;